import refs from './js/refs'

import { galleryElementsMarkup } from './js/gallery-markup'
import { onGalleryContainerClick } from './js/gallery-image'
import { onCloseModalWindow, onBackdropClick } from './js/modal-window'
import { addSrcAttrToLazyImages, addLazySizesScript} from './js/lazy-loading'

// –°–æ–∑–¥–∞–Ω–∏–µ –∏ —Ä–µ–Ω–¥–µ—Ä —Ä–∞–∑–º–µ—Ç–∫–∏ –ø–æ –º–∞—Å—Å–∏–≤—É –¥–∞–Ω–Ω—ã—Ö galleryItems –∏–∑ app.js –∏ –ø—Ä–µ–¥–æ—Å—Ç–∞–≤–ª–µ–Ω–Ω–æ–º—É —à–∞–±–ª–æ–Ω—É.
refs.galleryContainerEl.insertAdjacentHTML('beforeend', galleryElementsMarkup)

// –†–µ–∞–ª–∏–∑–∞—Ü–∏—è –¥–µ–ª–µ–≥–∏—Ä–æ–≤–∞–Ω–∏—è –Ω–∞ –≥–∞–ª–µ—Ä–µ–µ ul.js-gallery –∏ –ø–æ–ª—É—á–µ–Ω–∏–µ url –±–æ–ª—å—à–æ–≥–æ –∏–∑–æ–±—Ä–∞–∂–µ–Ω–∏—è.
refs.galleryContainerEl.addEventListener('click', onGalleryContainerClick)

// –û—Ç–∫—Ä—ã—Ç–∏–µ –º–æ–¥–∞–ª—å–Ω–æ–≥–æ –æ–∫–Ω–∞ –ø–æ –∫–ª–∏–∫—É –Ω–∞ —ç–ª–µ–º–µ–Ω—Ç–µ –≥–∞–ª–µ—Ä–µ–∏.
// –ó–∞–∫—Ä—ã—Ç–∏–µ –º–æ–¥–∞–ª—å–Ω–æ–≥–æ –æ–∫–Ω–∞ –ø–æ –∫–ª–∏–∫—É –Ω–∞ –∫–Ω–æ–ø–∫—É, –∫–Ω–æ–ø–∫—É "ESC" –∏ –∫–ª–∏–∫—É –≤ –±–µ–∫–¥–æ—Ä–æ–ø. –ü—Ä–æ–ª–∏—Å—Ç—ã–≤–∞–Ω–∏–µ –∏–∑–æ–±—Ä–∞–∂–µ–Ω–∏–π
// –û—á–∏—Å—Ç–∫–∞ –∑–Ω–∞—á–µ–Ω–∏—è –∞—Ç—Ä–∏–±—É—Ç–∞ src —ç–ª–µ–º–µ–Ω—Ç–∞ img.lightbox__image. 
refs.lightboxBtnEl.addEventListener('click', onCloseModalWindow)
refs.lightboxOverlayEl.addEventListener('click', onBackdropClick)

// –õ–µ–Ω–∏–≤–∞—è –∑–∞–≥—Ä—É–∑–∫–∞ –∏–∑–æ–±—Ä–∞–∂–µ–Ω–∏–π
if ('loading' in HTMLImageElement.prototype) {
  console.log('–ë—Ä–∞—É–∑–µ—Ä –ø–æ–¥–¥–µ—Ä–∂–∏–≤–∞–µ—Ç lazyload');
  addSrcAttrToLazyImages();
} else {
  console.log('–ë—Ä–∞—É–∑–µ—Ä –ù–ï –ø–æ–¥–¥–µ—Ä–∂–∏–≤–∞–µ—Ç lazyload');
  addLazySizesScript();
}

















